  .navbar-brandnya {
    font-size: 28px !important;
  }
  .navlink {
    width: 450px;
  }
  .banner {
    background-position-x: -120px;
    padding-top: 28%;
  }
  .banner .container h1 {
    font-size: 84px;
    letter-spacing: 7px;
  }
  .banner .container p {
    font-size: 30px;
    letter-spacing: 2px;
  }
  .tombol {
    margin-top: 38px;
  }
  .banner .container a button {
    font-size: 20px;
    width: 210px;
    height: 48px;
  }
}
.namaProduk {
  font-size: 12px;
}
.hargaProduk {
  font-size: 14px;
}

@media (max-width: 992px) {
  .navbar-brand {
    font-size: 24px !important;
  }
  .navbar-brandnya {
    font-size: 26px !important;
  }
  .navlink {
    width: 400px !important;
  }
  .banner {
    background-position-x: -220px;
    padding-top: 38% !important;
  }
  .banner .container h1 {
    font-size: 70px !important;
    letter-spacing: 5px;
  }
  .banner .container p {
    font-size: 30px !important;
    letter-spacing: 2px;
  }
  .tombol {
    margin-top: 35px;
    display: inline !important;
  }
  .banner .container a button {
    margin-right: 10px !important;
    font-size: 20px;
    width: 200px;
    height: 47px;
  }
  .lebihBanyak {
    padding: 5px 20px 5px 20px;
  }
}
