  .navbar-brandnya {
    font-size: 20px !important;
  }
  .navlink {
    width: 90% !important;
  }
  .navlink a::before {
    display: none;
  }
  .banner {
    background-position-x: -320px;
    padding-top: 200px;
  }
  .banner .container h1 {
    font-size: 46px !important;
    letter-spacing: 5px;
  }
  .banner .container p {
    font-size: 16px !important;
    letter-spacing: 2px;
  }

  .tombol {
    margin-top: 15px;
